import React from "react"
import { Link } from "gatsby"

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux'
import { toggleDarkMode } from '../containers/products/actions'

import Layout from "../components/layout"
import Image from "../components/image"
import SEO from "../components/seo"

const IndexPage = ({ isDarkMode, toggleDarkMode }) => (
  <Layout>
    <SEO title="Home" keywords={[`gatsby`, `application`, `react`]} />
    <h1>Hi people</h1>
    <p>Welcome to your new Gatsby site.</p>
    <p>Now go build something great.</p>
    <button
      style={isDarkMode ? { background: 'black' } : { background: 'white' }}
      onClick={() => toggleDarkMode(!isDarkMode)} > click me </button>

    <div style={{ maxWidth: `300px`, marginBottom: `1.45rem` }}>
      <Image />
    </div>
    <Link to="/page-2/">Go to page 2</Link>
  </Layout>
)

const mapStateToProps = state => ({
  isDarkMode: state.productReducer.isDarkMode,
});

const mapDispatchToProps = dispatch => ({
  toggleDarkMode: bindActionCreators(toggleDarkMode, dispatch),
})
export default connect(mapStateToProps, mapDispatchToProps)(IndexPage)
